import {Link} from "gatsby";
import React from "react";

export default function Header() {

    const [menuOpen, setMenuOpen] = React.useState(false);

    return (
        <nav className={"flex justify-between"}>
            <Link to={"/"}>
                <h1 className="text-3xl text-black leading-normal mb-6 mt-0 inline-block">xprilion's blog</h1>
            </Link>
            <div>
                {/* Menu for large screens */}
                <div className="hidden sm:flex">
                    <Link to="/posts" className="m-1">Posts</Link>&middot;
                    <Link to="/talks" className="m-1">Talks</Link>&middot;
                    <Link to="/publications" className="m-1">Publications</Link>
                </div>

                <button
                    type="button"
                    className="flex items-center justify-center w-10 h-10 sm:hidden text-3xl rounded-full border-2 border-black"
                    onClick={() => setMenuOpen(!menuOpen)}
                    style={{
                        backgroundImage: "url(/ellipses.png)",
                        backgroundSize: "50%",
                        backgroundPosition: "50%",
                        backgroundRepeat: "no-repeat"
                    }}
                >
                </button>

                {/* Collapsed menu for smaller screens */}
                {menuOpen && (
                    <div className="sm:hidden" id={"small-nav-menu"}>
                        <Link to="/posts" className="m-1">Posts</Link>&middot;
                        <Link to="/talks" className="m-1">Talks</Link>&middot;
                        <Link to="/publications" className="m-1">Publications</Link>
                    </div>
                )}
            </div>
        </nav>
    )
}