import * as React from "react"
import {graphql, Link} from "gatsby";
import type { HeadFC, PageProps } from "gatsby"
import Layout from "../components/layout";
import {formatDate} from "../utils/date";
import {SignupForm} from "../components/signup-form";

interface IndexPageProps {
    data: {
        hashnode: {
            publication: {
                posts: {
                    edges: Array<{
                        node: {
                            title: string;
                            slug: string;
                            publishedAt: string;
                        }
                    }>
                }
            }
        }
    }
}

const IndexPage = ({data } : IndexPageProps) => {
    const posts = data.hashnode.publication.posts.edges;

    return (
        <Layout>
            <ul className={"p-0"}>
                {posts.map((post: any) => (
                    <li style={{display: "block"}} key={post.node.slug}>
                        <Link style={{ boxShadow: `none` }} to={`/${post.node.slug}`}>
                            <h2 style={{ marginBottom: `0.2rem` }}>
                            {post.node.title}
                            </h2>
                        </Link>
                        <small>{formatDate(post.node.publishedAt)}</small>
                        <p style={{ marginTop: `0.5rem` }}>{post.node.brief}</p>
                    </li>
                ))}
            </ul>
            <br />
            <SignupForm />
            <br />
        </Layout>
    )
}

export default IndexPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    hashnode {
      publication(host: "xprilion.hashnode.dev") {
        isTeam
        title
        posts(first: 20) {
          edges {
            node {
                id
                slug
                title
                brief
                url
                publishedAt
            }
          }
        }
      }
    }
  }
`